<template>
  <div class="tw-multicol">
    <div class="tw-multicol-left">
      <!--  -->
      <tw-collapse-group class="tw-sidebar" v-if="$route.params.type==='guid'">
        <ul class="tw-nav xsidebar">
          <li><router-link to="/doc/guid/design" class="jst-close"><i class="tw-font xico"></i></router-link></li>
          <li><router-link to="/doc/guid/version" class="jst-close"><i class="tw-font xico"></i></router-link></li>
          <li><router-link to="/doc/guid/dir" class="jst-close"><i class="tw-font xico"></i></router-link></li>
          <li><router-link to="/doc/guid/js" class="jst-close"><i class="tw-font xico"></i>JS</router-link></li>
          <li><router-link to="/doc/guid/css" class="jst-close"><i class="tw-font xico"></i>CSS</router-link></li>
          <li><router-link to="/doc/guid/html" class="jst-close"><i class="tw-font xico"></i>HTML</router-link></li>
          <li><router-link to="/doc/guid/shortcut" class="jst-close"><i class="tw-font xico"></i></router-link></li>
        </ul>
      </tw-collapse-group>
      <!-- / -->

      <!--  -->
      <tw-collapse-group class="tw-sidebar" v-if="$route.params.type==='component'">
        <ul class="tw-nav xsidebar">
          <li><a class="jst-close"><i class="tw-font xico"></i>SCSS</a></li>
          <li>
            <a class="js-layout"><i class="tw-font xico"></i><i class="tw-arrow xright"></i></a>
            <tw-collapse class="xsidebar" switch=".js-layout">
              <ul class="tw-nav xsidebar">
                <li><a></a></li>
                <li><a></a></li>
              </ul>
            </tw-collapse>
          </li>
          <li>
            <a class="js-nav"><i class="tw-font xico"></i><i class="tw-arrow xright"></i></a>
            <tw-collapse class="xsidebar" switch=".js-nav">
              <ul class="tw-nav xsidebar">
                <li><a></a></li>
                <li><a></a></li>
                <li><a></a></li>
              </ul>
            </tw-collapse>
          </li>
          <li>
            <a class="js-unit"><i class="tw-font xico"></i><i class="tw-arrow xright"></i></a>
            <tw-collapse class="xsidebar" switch=".js-unit">
              <ul class="tw-nav xsidebar">
                <li><a></a></li>
                <li><a></a></li>
              </ul>
            </tw-collapse>
          </li>
          <li>
            <a class="js-function"><i class="tw-font xico"></i><i class="tw-arrow xright"></i></a>
            <tw-collapse class="xsidebar" switch=".js-function">
              <ul class="tw-nav xsidebar">
                <li><a></a></li>
                <li><a></a></li>
              </ul>
            </tw-collapse>
          </li>
          <li>
            <a class="js-form"><i class="tw-font xico"></i><i class="tw-arrow xright"></i></a>
            <tw-collapse class="xsidebar" switch=".js-form">
              <ul class="tw-nav xsidebar">
                <li><a>/</a></li>
                <li><a></a></li>
              </ul>
            </tw-collapse>
          </li>
          <li><a class="jst-close"><i class="tw-font xico"></i></a></li>
        </ul>
      </tw-collapse-group>
      <!-- / -->
    </div>
    <div class="tw-multicol-cell">
      <div class="tw-multicol-cell-table">
        <div class="tw-multicol-cell-cell">
          <tw-markdown :content="markdownText"></tw-markdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Markdown from '@tw/vue/markdown/markdown'

export default {
  name: 'intro',
  components: {
    twMarkdown: Markdown
  },
  data () {
    return {
      markdownText: ''
    }
  },
  methods: {
    updateMarkdown () {
      const vm = this

      vm.axios(`http://localhost:83/getMarkdown/?name=${vm.$route.params.name}`).then(function (responed) {
        vm.markdownText = responed.data
      }).catch(function (error) {
        console.log(error)
      })
    }
  },
  mounted () {
    this.updateMarkdown()
  },
  watch: {
    '$route' () {
      this.updateMarkdown()
    }
  }
}
</script>



