      vm.styleEl = document.createElement('style')
      document.querySelector('head').appendChild(vm.styleEl)

      vm.axios.get('http://localhost:83/getScssVars').then(function (responed) {
        vm.scssVars = responed.data
      }).catch(function (error) {
        console.log(error)
      })
    }
